export const validateAuth = ({ email, password, repeatedPassword }, error) => {
  const errors = {};

  if (!email) {
    errors.email = ' email';
  }

  if (!password) {
    errors.password = ' ';
  }
  if (repeatedPassword === '' && password) {
    errors.password = ' ';
  }

  if (repeatedPassword && repeatedPassword !== password) {
    errors.password = '  ';
  }

  if (password && password.length < 5) {
    errors.passwordLength = '     5 ';
  }

  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  if (email && !emailRegex.test(email)) {
    errors.email = '  email';
  }

  if (error) {
    errors.error = error;
  }

  if (Object.keys(errors).length > 0) {
    return errors;
  }

  return null;
};

export const validateProfile = ({ name, surname, telephone, identifier }, error) => {
  const errors = {};

  if (!name) {
    errors.name = ' ';
  }

  if (!surname) {
    errors.surname = ' ';
  }
  if (!telephone) {
    errors.telephone = ' ';
  }
  if (!identifier) {
    errors.identifier = ' ';
  }

  if (error) {
    errors.error = error;
  }

  if (Object.keys(errors).length > 0) {
    return errors;
  }

  return null;
};
